<template>
  <div class="wrapper">
    <div class="header">
      <div class="mask"></div>
      <div class="result">
        <h1>EVE</h1>
        <div class="calculate-result">
          <div class="detail">
            <b>总技能点：{{total}}</b>
          </div>
          <div class="btns">
            <a href="javascript:;" class="link" @click="clear">清空</a>
            <!--<a href="javascript:;" class="link" @click="showGT">吉他脑浆</a>-->
          </div>
        </div>
      </div>
    </div>
    <div class="panel clearfix">
      <div class="left">
        <ul>
          <li v-for="(item,index) in tree" :class="index===listActive?'active':''" @click="listClick(index,item.id)"><span>{{item.name}}</span><i class="line animated"></i></li>
        </ul>
      </div>
      <div class="right">
        <table v-if="!loading">
          <template v-for="(item,index) in skillList">
            <tr>
              <td class="skill-icon"><i class="fa fa-book"></i></td>
              <td class="skill-name"><b>{{item.name}} {{item.ratio?item.ratio+'X':''}}</b></td>
              <td>
                <p v-html="item.descript"></p>
              </td>
            </tr>
            <tr colspan="2">
              <td></td>
              <td>技能点数：<b>{{selectedSkills[item.id]||0}}</b></td>
            </tr>
            <tr>
              <td colspan="3" class="process">
                <div class="slider">
                  <el-slider v-model="sliders[item.id]" range show-stops :max="5" :min="0" @change="(val)=>change(val,item.id,item.ratio)"></el-slider>
                </div>
              </td>
            </tr>
            <tr>
              <td colspan="3" class="spline"><i class="line"></i></td>
            </tr>
          </template>
        </table>

      </div>
    </div>
    <div class="table">

    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        loading: true,
        tree: [],
        subList: {},
        listActive: 0,
        skillList: [],
        rateNum: {
          1: { 0: 0, 1: 250, 2: 1415, 3: 8000, 4: 45255, 5: 256000 },
          2: { 0: 0, 1: 500, 2: 2829, 3: 16000, 4: 90510, 5: 512000 },
          3: { 0: 0, 1: 750, 2: 4243, 3: 24000, 4: 135765, 5: 768000 },
          4: { 0: 0, 1: 1000, 2: 5657, 3: 32000, 4: 181020, 5: 1024000 },
          5: { 0: 0, 1: 1250, 2: 7072, 3: 8000, 4: 45255, 5: 1280000 },
          6: { 0: 0, 1: 1500, 2: 8486, 3: 48000, 4: 271530, 5: 1536000 },
          7: { 0: 0, 1: 1750, 2: 9900, 3: 56000, 4: 316784, 5: 1792000 },
          8: { 0: 0, 1: 2000, 2: 11314, 3: 64000, 4: 362039, 5: 2048000 },
          9: { 0: 0, 1: 2250, 2: 12728, 3: 72000, 4: 407294, 5: 2304000 },
          10: { 0: 0, 1: 2500, 2: 14143, 3: 80000, 4: 452549, 5: 2560000 },
          11: { 0: 0, 1: 2750, 2: 15557, 3: 88000, 4: 497804, 5: 2816000 },
          12: { 0: 0, 1: 3000, 2: 16971, 3: 96000, 4: 543058, 5: 3072000 },
          13: { 0: 0, 1: 3250, 2: 18385, 3: 104000, 4: 588313, 5: 3328000 },
          14: { 0: 0, 1: 3500, 2: 19799, 3: 112000, 4: 633568, 5: 3584000 },
          15: { 0: 0, 1: 3750, 2: 21214, 3: 120000, 4: 678823, 5: 3840000 },
          16: { 0: 0, 1: 4000, 2: 22628, 3: 128000, 4: 724078, 5: 4096000 }
        },
        sliders: {

        },
        total: 0 ,
        selectedSkills: {}
      }
    },
    created() {
      this.loading = true;
      fetch('http://123.206.80.123:3000/api/getSkillTree')
        .then(function (res) {
          return res.json();
        })
        .then((data) => {
          var _slider = {}
          this.tree = data.data;
          this.skillList = data.data[0].skills;
          this.tree.forEach((v) => {
            this.subList[v.id] = v.skills;
          })
          this.tree.forEach((v) => {
            v.skills.forEach((v, i) => {
              _slider[v.id] = [0, 0];
            })
          })
          this.sliders = _slider;
          this.loading = false;
        })
    },
    methods: {
      change(val, id, ratio) {
        var point = this.rateNum[ratio];
        this.selectedSkills[id] = point[val[1]] - point[val[0]];
        this.calculate();
      },
      listClick(index, id) {
        this.listActive = index;
        this.skillList = this.subList[id];
      },
      calculate() {
        var key;
        var sum = 0;
        for (key in this.selectedSkills) {
          sum += this.selectedSkills[key];
        }
        this.total = sum;
      },
      clear() {
        var _slider = {};
        this.tree.forEach((v) => {
          v.skills.forEach((v, i) => {
            _slider[v.id] = [0, 0];
          })
        })
        this.selectedSkills = {};
        this.sliders = _slider;
        this.calculate();
      }
    }
  }

</script>

<style lang="scss">
  @import './style.scss';
</style>


