import Vue from 'vue';
import router from './router.js';
import Vuex from 'vuex';
import immutable from 'immutable';
import Index from './Index.vue';
import {Slider} from 'element-ui'

Vue.use(Vuex);
Vue.component(Slider.name,Slider);
const store = new Vuex.Store({
  state:{
    map:immutable.Map({})
  },
  mutations:{
    mapSet(state, {key,val}) {
      if(typeof val==='object'){
        val = immutable.fromJS(val);
      }
      state.map = state.map.setIn(key,val);   
    },
    mapDelete(state, {key}) {
      state.map = state.map.deleteIn(key);  
    }
  },
  actions:{
    mapSet(ctx,data) {
      ctx.commit('mapSet', data)
    },
    mapDelete(ctx,data) {
      ctx.commit('mapDelete',data)
    }
  }
})

new Vue({
  el: '#app',
  store,
  router,
  template:'<Index />',
  components:{Index}
})



